import React from "react";

export default function Project() {
  return (
    <div id="carouselExample" className="carousel slide">
      <br />
      <br />
      <h5 className="card-title IBM-Plex-Mono text-center fs-3">Projects</h5>
      <br />
      <br />
      <div className="carousel-inner" id="projects">
        <div className="carousel-item active">
          <div className="card text-bg-dark project-card">
            <img
              src="https://www.linkpicture.com/q/Screenshot-31_9.png"
              className="card-img d-block w-100"
              alt="..."
            />
            <div className="card-img-overlay">
              <div className="container align-items-end text-center">
                <div className="row">
                  <div className="col-md-6 ms-md-auto">
                    <div>
                      {/* Button trigger modal */}
                      <button
                        type="button"
                        className="btn btn-dark"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal1"
                      >
                        Launch demo modal
                      </button>
                      {/* Modal */}
                      <div
                        className="modal fade"
                        id="exampleModal1"
                        tabIndex={-1}
                        aria-labelledby="exampleModalLabel"
                        aria-hidden="true"
                      >
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h1
                                className="modal-title fs-5"
                                id="exampleModalLabel"
                              >
                                Modal title
                              </h1>
                              <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                              />
                            </div>
                            <div className="modal-body">...</div>
                            <div className="modal-footer">
                              <button
                                type="button"
                                className="btn btn-secondary"
                                data-bs-dismiss="modal"
                              >
                                Close
                              </button>
                              <button type="button" className="btn btn-primary">
                                Save changes
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="carousel-item">
          <div className="card text-bg-dark project-card">
            <img
              src="https://www.linkpicture.com/q/Screenshot-35_9.png"
              className="card-img d-block w-100"
              alt="mailchinmp"
            />
            <div className="card-img-overlay  ">
              <div className="container align-items-end text-center">
                <div className="row">
                  <div className="col-md-6 ms-md-auto">
                    <div>
                      {/* Button trigger modal */}
                      <button
                        type="button"
                        className="btn btn-dark"
                        data-bs-toggle="modal"
                        data-bs-target="#exampleModal"
                      >
                        Launch demo modal
                      </button>
                      {/* Modal */}
                      <div
                        className="modal fade"
                        id="exampleModal"
                        tabIndex={-1}
                        aria-labelledby="exampleModalLabel"
                        aria-hidden="true"
                      >
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h1
                                className="modal-title fs-5"
                                id="exampleModalLabel"
                              >
                                Modal title
                              </h1>
                              <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                              />
                            </div>
                            <div className="modal-body">...</div>
                            <div className="modal-footer">
                              <button
                                type="button"
                                className="btn btn-secondary"
                                data-bs-dismiss="modal"
                              >
                                Close
                              </button>
                              <button type="button" className="btn btn-primary">
                                Save changes
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <button
        className="carousel-control-prev"
        type="button"
        data-bs-target="#carouselExample"
        data-bs-slide="prev"
      >
        <span className="carousel-control-prev-icon" aria-hidden="true" />
        <span className="visually-hidden">Previous</span>
      </button>
      <button
        className="carousel-control-next"
        type="button"
        data-bs-target="#carouselExample"
        data-bs-slide="next"
      >
        <span className="carousel-control-next-icon" aria-hidden="true" />
        <span className="visually-hidden">Next</span>
      </button>
    </div>
  );
}
