import React from "react";
import { Link } from "react-scroll";

export default function Navbar(Props) {
  return (
    <nav
      className="navbar sticky-top bg-opacity-50 navbar-expand-lg bg-dark border border-dark shadow p-3 mb-5 bg-body rounded"
      id="nav-menu"
    >
      <div className="container-fluid">
        <a
          class="nav-link home"
          className="navbar-brand"
          href="#"
          aria-current="page"
          smooth={true}
          duration={100}
          id="home"
          
        >
          <img
            src="https://img.icons8.com/fluency/256/sound-recording-copyright.png"
            alt="Bootstrap"
            width={30}
            height={30}
          />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0 mx-auto">
            <li className="nav-item">
              <Link
                className="nav-link about IBM-Plex-Mono"
                aria-current="page"
                to="about"
                smooth={true}
                duration={100}
              >
                About
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link skills IBM-Plex-Mono"
                aria-current="page"
                to="skills"
              >
                Skills
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link projects IBM-Plex-Mono"
                aria-current="page"
                to="projects"
              >
                Projects
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link contact IBM-Plex-Mono"
                aria-current="page"
                to="contact"
              >
                Contact
              </Link>
            </li>
            <li className="nav-item">
              <a
                id="resume-link-1 nav-link resume"
                className="btn btn-outline-secondary IBM-Plex-Mono btn-hover nav-link resume"
                aria-current="page"
                href="https://drive.google.com/file/d/1Qw09sia4nURU-e98OHywHfOCQs1kzHZv/view?usp=sharing"
              >
                Resume
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
